* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--bg-primary);
  color: var(--text-primary);
  line-height: 1.6;
  transition: background-color 0.3s ease, color 0.3s ease;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Coffee flat-white theme colors (light) */
:root {
  --flat-white: #F5F5DC;
  --coffee-brown: #2C1810;
  --latte: #E6D7C3;
  --espresso: #1A0F0A;
  --cappuccino: #D4C4A8;
  --mocha: #8B7355;
  --cream: #FDFBF7;
  --steam: #F8F6F0;
  
  /* Light theme variables */
  --bg-primary: #F5F5DC;
  --bg-secondary: #FDFBF7;
  --bg-card: #F8F6F0;
  --text-primary: #2C1810;
  --text-secondary: #8B7355;
  --border-primary: #D4C4A8;
  --border-secondary: #E6D7C3;
  --accent-primary: #1A0F0A;
  --accent-secondary: #8B7355;
}

/* Dark coffee theme colors */
@media (prefers-color-scheme: dark) {
  :root {
    --bg-primary: #1A0F0A;
    --bg-secondary: #2C1810;
    --bg-card: #3C2A1A;
    --text-primary: #E6D7C3;
    --text-secondary: #D4C4A8;
    --border-primary: #8B7355;
    --border-secondary: #5D4A3A;
    --accent-primary: #F5F5DC;
    --accent-secondary: #D4C4A8;
  }
} 